import React from 'react'
import Carousel from '../../h5/component/carousel'

const imgArr = ['../images/1.jpg', '../images/2.jpg', '../images/3.jpg', '../images/4.jpg'];

export default class CarouselExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="container">
                <p className="label">基本</p>
                <Carousel
                    width = { '100%' }
                    height = { '30vh' }
                >
                    {
                        imgArr && imgArr.map((url, index) =>
                        <img key = { index } src = { url }/>)
                    }
                </Carousel>
                <br/>
                <p className="label">手动播放</p>
                <Carousel
                    width = { '100%' }
                    height = { '30vh' }
                    autoPlay = {false} 
                >
                    {
                        imgArr && imgArr.map((url, index) =>
                        <img key = { index } src = { url }/>)
                    }
                </Carousel>
                <br/>
                <p className="label">开始索引</p>
                <Carousel
                    width = { '100%' }
                    height = { '30vh' }
                    index = {2} 
                >
                    {
                        imgArr && imgArr.map((url, index) =>
                        <img key = { index } src = { url }/>)
                    }
                </Carousel>
                <br/>
                <p className="label">轮播间隔时长</p>
                <Carousel
                    width = { '100%' }
                    height = { '30vh' }
                    autoPlayTimeout = { 1000 }
                >
                    {
                        imgArr && imgArr.map((url, index) =>
                        <img key = { index } src = { url }/>)
                    }
                </Carousel>
            </div>
        )
    }
}


