import React from 'react'
import Card from '../../h5/component/card'

export default class CardExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="container">
                <Card >
                    <Card.Body>
                        <p style={{fontSize: '0.3rem'}} >只有内容</p>
                    </Card.Body>
                </Card>
                <br/>
                <Card >
                    <Card.Header>
                        <p style={{fontSize: '0.3rem'}} >图片</p>
                    </Card.Header>
                    <Card.Body>
                        <img src={'../images/1.jpg'}/>
                    </Card.Body>
                </Card>
                <br/>
                <Card >
                    <Card.Footer>
                        <p style={{fontSize: '0.3rem'}} >底部左侧</p>
                        <p style={{fontSize: '0.3rem'}} >底部右侧</p>
                    </Card.Footer>
                </Card>
                <br/>
                <Card >
                    <Card.Header>
                        <p style={{fontSize: '0.3rem'}} >这是一个标题</p>
                    </Card.Header>
                    <Card.Body>
                        <p style={{fontSize: '0.3rem'}} >这是一个同时使用到了Card.Header、Card.Body、Card.Footer的卡片。</p>
                    </Card.Body>
                    <Card.Footer>
                        <p style={{fontSize: '0.3rem'}} >底部左侧</p>
                        <p style={{fontSize: '0.3rem'}} >底部右侧</p>
                    </Card.Footer>
                </Card>
            </div>
        )
    }
}


