import React from 'react'
import Icon from '../../h5/component/icon'
import IconMap from '../../h5/component/icon/iconMap.json'
import Flex from '../../h5/component/flex'

export default class IconExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        {/** h5新增功能，flex属性小于0时不再为容器设置flex属性，否则的话width就没法生效了*/}
        return (
            <div className="container">
                <Flex wrap={'wrap'} justify={'center'} >
                    {
                        Object.keys(IconMap).map(item => 
                            <Flex.Item flex={-1} key={item} flexItemStyle={{width: '1.5rem', padding: '0 0.2rem', textAlign: 'center'}}>
                                <Icon name={item}/>
                            </Flex.Item>
                        )
                    }
                </Flex>
                
            </div>
        )
    }
}


