import React from 'react'
import Switch from '../../h5/component/switch'

export default class SwitchExample extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            value: false,
            value1: true,
            value2: false
        }
    }
    render() {
        return (
            <div className="container">
                <p className={'label'}>基本</p>
                <Switch value = {this.state.value} onValueChange = {value => this.setState({value})}/>
                <br/>
                <p className={'label'}>禁用</p>
                <Switch value = {this.state.value1} disabled onValueChange = {value1 => this.setState({value1})}/>
                <br/>
                <p className={'label'}>自定义颜色</p>
                <Switch
                    tintColor = {'gray'}
                    onTintColor = {'#f75b25'}
                    thumbTintColor = {'#68b1ed'}
                    tintBorderColor = {'gray'}
                    onTintBorderColor = {'#f75b25'}
                    thumbBorderColor = {'#68b1ed'}
                    value = {this.state.value2}
                    onValueChange = {value2 => this.setState({value2})}
                />
            </div>
        )
    }
}


