import React from 'react'
import ReactDOM from 'react-dom'
//使用阿里flexible解决方案
//import 'amfe-flexible';
import Home from "../components/home";
import { Router, Route, Link, hashHistory, IndexRoute } from 'react-router'
import List from '../h5/component/list'
import ActionSheet from '../components/home/actionSheetExample'
import Badge from '../components/home/badgeExample'
import Button from '../components/home/buttonExample'
import Card from '../components/home/cardExample'
import Carousel from '../components/home/carouselExample'
import Checkbox from '../components/home/checkboxExample'
import Collapse from '../components/home/collapseExample'
import Counter from '../components/home/counterExample'
import DrawerLayout from '../components/home/drawerLayoutExample'
import Flex from '../components/home/flexExample'
import Header from '../components/home/headerExample'
import Icon from '../components/home/iconExample'
import ListExample from '../components/home/listExample'
import PaginationExample from '../components/home/paginationExample'
import RadioExample from '../components/home/radioExample'
import SearchExample from '../components/home/searchExample'
import SliderExample from '../components/home/sliderExample'
import SwitchExample from '../components/home/switchExample'
import TabbarExample from '../components/home/tabbarExample'
import ToastExample from '../components/home/toastExample'
import Toast from '../h5/component/toast'

const Lists = () => (
    <div>
        <div className="list-container">
            <List title={'ActionSheet'} onPress={() => hashHistory.push('actionSheet')} showLine lineMargin={30}/>
            <List title={'Badge'} onPress={() => hashHistory.push('badge')} showLine lineMargin={30}/>
            <List title={'Button'} onPress={() => hashHistory.push('button')} showLine lineMargin={30}/>
            <List title={'Card'} onPress={() => hashHistory.push('card')} showLine lineMargin={30}/>
            <List title={'Carousel'} onPress={() => hashHistory.push('carousel')} showLine lineMargin={30}/>
            <List title={'Checkbox'} onPress={() => hashHistory.push('checkbox')} showLine lineMargin={30}/>
            <List title={'Collapse'} onPress={() => hashHistory.push('collapse')} showLine lineMargin={30}/>
            <List title={'Counter'} onPress={() => hashHistory.push('counter')} showLine lineMargin={30}/>
            <List title={'DrawerLayout'} onPress={() => hashHistory.push('drawerLayout')} showLine lineMargin={30}/>
            <List title={'Flex'} onPress={() => hashHistory.push('flex')} showLine lineMargin={30}/>
            <List title={'Header'} onPress={() => hashHistory.push('header')} showLine lineMargin={30}/>
            <List title={'Icon'} onPress={() => hashHistory.push('icon')} showLine lineMargin={30}/>
            <List title={'List'} onPress={() => hashHistory.push('list')} showLine lineMargin={30}/>
            <List title={'Pagination'} onPress={() => hashHistory.push('pagination')} showLine lineMargin={30}/>
            <List title={'Radio'} onPress={() => hashHistory.push('radio')} showLine lineMargin={30}/>
            <List title={'Search'} onPress={() => hashHistory.push('search')} showLine lineMargin={30}/>
            <List title={'Slider'} onPress={() => hashHistory.push('slider')} showLine lineMargin={30}/>
            <List title={'Switch'} onPress={() => hashHistory.push('switch')} showLine lineMargin={30}/>
            <List title={'Tabbar'} onPress={() => hashHistory.push('tabbar')} showLine lineMargin={30}/>
            <List title={'Toast'} onPress={() => hashHistory.push('toast')}/>
        </div>
    </div>
)

const routes = {
    path: '/',
    component: Home,
    indexRoute: { component: Lists },
    childRoutes: [
        { path: 'actionSheet', component: ActionSheet },
        { path: 'badge', component: Badge },
        { path: 'button', component: Button },
        { path: 'card', component: Card },
        { path: 'carousel', component: Carousel },
        { path: 'checkbox', component: Checkbox },
        { path: 'collapse', component: Collapse },
        { path: 'counter', component: Counter },
        { path: 'drawerLayout', component: DrawerLayout },
        { path: 'flex', component: Flex },
        { path: 'header', component: Header },
        { path: 'icon', component: Icon },
        { path: 'list', component: ListExample },
        { path: 'pagination', component: PaginationExample },
        { path: 'radio', component: RadioExample },
        { path: 'search', component: SearchExample },
        { path: 'slider', component: SliderExample },
        { path: 'switch', component: SwitchExample },
        { path: 'tabbar', component: TabbarExample },
        { path: 'toast', component: ToastExample }
    //   { path: 'inbox', component: Inbox },
    ]
}

class Root extends React.Component {
    constructor(props) {
        super(props)
    }
    componentDidMount() {
        window.addEventListener('showToast', e => {
          this.rootToast.show(window.toastConfig);
        })
    }
    render() {
        return (
            <div style={{height:'100%'}}>
                <Toast ref = {toast => this.rootToast = toast} />
                <Router history={hashHistory} routes={routes}/>
            </div> 
        )
    }
}

if ('addEventListener' in document) {
    document.addEventListener('DOMContentLoaded', function() {
        FastClick.attach(document.body);
    }, false);
}


ReactDOM.render(<Root/>, document.getElementById('app'))



