import React from 'react'
import Toast from '../../h5/component/toast'
import Button from '../../h5/component/button'

export default class ToastExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="container">
                <Button onPress = {() => this.toast.show({
                    content: '基本',
                    })} >基本</Button>
                <br/>
                <Button onPress = {() => this.toast.show({
                    content: '取消遮罩层',
                    mask: false
                    })} >取消遮罩层</Button>
                <Toast ref = {toast => this.toast = toast} />
                <br/>
                <Button onPress = {() => {
                    window.toastConfig = {
                        content: '全局提示',
                        mask: false
                    }
                    window.dispatchEvent(new Event('showToast'))
                }} >全局提示</Button>
            </div>
        )
    }
}


