import React from 'react'
import Search from '../../h5/component/search'

export default class SearchExample extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            value: '受控'
        }
    }
    render() {
        return (
            <div className="container">
                <Search
                    placeholder = {'输入任何内容'}
                />
                <br/>
                <Search
                    placeholder = {'「取消」常显'}
                    showCancelButton
                />
                <br/>
                <Search
                    placeholder = {'自定义取消文本'}
                    showCancelButton
                    cancelText = {'关闭'}
                />
                <br/>
                <Search
                    placeholder = {'显示模态框'}
                    showModal
                />
                <br/>
                <Search
                    placeholder = {'自定义模态框组件'}
                    showModal
                    modalComponent = {<p style={{fontSize: '0.4rem'}}>这里显示自定义内容</p>}
                />
                <br/>
                <Search
                    placeholder = {'禁用'}
                    disabled
                />
                <br/>
                <Search
                    defaultValue = {'默认值'}
                />
                <br/>
                <Search
                    value = {this.state.value}
                    onChange = {value => this.setState({value})}
                />
            </div>
        )
    }
}


