import React from 'react'
import Flex from '../../h5/component/flex'

export default class FlexExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="container">
                <Flex flexStyle = {{marginBottom: '0.4rem'}} >
                    <Flex.Item>
                        <div style = {{backgroundColor: '#108EE9'}} >
                            <p>平分</p>
                        </div>
                    </Flex.Item>
                    <Flex.Item>
                        <div style = {{backgroundColor: '#4badf3'}} >
                            <p>平分</p>
                        </div>
                    </Flex.Item>
                </Flex>
                <Flex 
                    justify = {'flex-end'} 
                    flexStyle = {{marginBottom: 20}} 
                >
                    {
                        (function() {
                        return [1,2,3].map((item, index) => {
                            return (
                            <div 
                                key = {index} 
                                style = {{marginLeft: 10, marginRight: 0, backgroundColor: '#8c62f9'}} 
                            >
                                <p>主轴 flex-end</p>
                            </div>
                            )
                        })
                        })()
                    }
                </Flex>
                <Flex 
                    align = {'flex-end'}
                    flexStyle = {{marginBottom: 20, height: '1rem', backgroundColor: '#eeeeee'}} 
                >
                    {
                        (function() {
                        return [1,2,3].map((item, index) => {
                            return (
                            <p 
                                key = {index} 
                                style = {{marginLeft: 10, marginRight: 0, backgroundColor: '#ff4f4f'}} 
                            >
                                次抽 flex-end
                            </p>
                            )
                        })
                        })()
                    }
                </Flex>
                <Flex flexStyle = {{marginBottom: 20}} wrap = {'wrap'} >
                    {
                        (function() {
                        return [1,2,3,5,6,7,8,9].map((item, index) => {
                            return (
                            <div 
                                key = {index} 
                                style = {{marginLeft: 10, marginRight: 0, marginTop: 10, backgroundColor: '#11b7c0'}} 
                            >
                                <p>flex-wrap wrap</p>
                            </div>
                            )
                        })
                        })()
                    }
                </Flex>
                <Flex justify = {'center'} align = {'center'} flexStyle = {{width: '5rem', height: '3rem', backgroundColor: '#afb50c'}}>
                    <p>水平垂直居中</p>
                </Flex>
            </div>
        )
    }
}


