import React from 'react'
import Counter from '../../h5/component/counter'


export default class CounterExample extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            value: 2
        }
    }
    render() {
        return (
            <div className="container">
                <p className={'label'}>默认值</p>
                <Counter defaultValue = {5} />
                <br/>
                <p className={'label'}>最大值、最小值</p>
                <Counter minValue = {10} maxValue = {15} />
                <br/>
                <p className={'label'}>步长</p>
                <Counter stepNum = {3} />
                <br/>
                <p className={'label'}>禁用</p>
                <Counter disabled />
                <br/>
                <p className={'label'}>受控</p>
                <Counter value={this.state.value} onChange={value => this.setState({value})} stepNum = {2} minValue = {1} maxValue = {9} />
            </div>
        )
    }
}


