import React from 'react'
import DrawerLayout, { DRAWER_POSITION } from '../../h5/component/drawerLayout'
import List from '../../h5/component/list'
import Button from '../../h5/component/button'

export default class DrawerLayoutExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        const drawerView = (
            <div>
                <List title = {'前往商城'} showLine />
                <List title = {'个人中心'} showLine />
                <List title = {'设置'} showLine />
                <p style={{fontSize: '0.4rem'}}>I'm in the Drawer!</p>
            </div>
        )
        return (
            <div>
                <DrawerLayout 
                    drawerPosition = {DRAWER_POSITION.RIGHT} 
                    renderNavigationView = {() => drawerView} 
                    ref = {drawer => this.drawer1 = drawer} 
                >
                    {/* <Button 
                            onPress = {() => this.drawer.openDrawer()} 
                        >打开左侧抽屉</Button>
                        <DrawerLayout 
                            renderNavigationView = {() => drawerView} 
                            ref = {drawer => this.drawer = drawer} 
                        />
                    <br/> */}
                    <Button 
                        onPress = {() => this.drawer1.openDrawer()} 
                    >打开右侧抽屉</Button>
                </DrawerLayout>
                <br/>
                <DrawerLayout 
                    renderNavigationView = {() => drawerView} 
                    ref = {drawer => this.drawer = drawer} 
                >
                    <Button 
                            onPress = {() => this.drawer.openDrawer()} 
                        >打开左侧抽屉</Button>
                </DrawerLayout>
            </div>
        )
    }
}


