import React from 'react'
import Slider from '../../h5/component/slider'
import Button from '../../h5/component/button'

export default class SliderExample extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            value: 0,
            slider: null
        }
    }
    render() {
        return (
            <div className="container">
                <p className={'label'}>基本</p>
                <Slider/>
                <br/>
                <p className={'label'}>宽度</p>
                <Slider width = {600}/>
                <br/>
                <p className={'label'}>当前位置</p>
                <Slider progress = {50}/>
                <br/>
                <p className={'label'}>禁用</p>
                <Slider progress = {50} disabled/>
                <br/>
                <p className={'label'}>受控</p>
                <p>{this.state.value}</p>
                <Slider progress = {this.state.value} onChange = {value => this.setState({value})}/>
                <br/>
                <p className={'label'}>自定义样式</p>
                <Slider
                    progress = {30}
                    minValue = {0}
                    maxValue = {99}
                    barColor = {'#eeeeee'}
                    ballColor = {'#b134e5'}
                    crossColor = {'#11b7c0'}
                />
                <br/>
                <p className={'label'}>手动获取值</p>
                <Slider ref = { slider => this.slider = slider }/>
                <br/>
                <Button onPress = { () => alert(this.slider.getValue()) }>获取</Button>
            </div>
        )
    }
}


