import React from 'react'
import Collapse from '../../h5/component/collapse'
import Toast from '../../h5/component/toast'

export default class CollapseExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="container">
                <p className="label">基本</p>
                <Collapse>
                    <Collapse.Panel title = {'标题 1'} panelKey = {'panel1'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 1 的内容</p>
                    </Collapse.Panel>
                    <Collapse.Panel title = {'标题 2'} panelKey = {'panel2'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 2 的内容</p>
                    </Collapse.Panel>
                    <Collapse.Panel title = {'标题 3'} panelKey = {'panel3'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 3 的内容</p>
                    </Collapse.Panel>
                </Collapse>
                <br/>
                <p className="label">激活项</p>
                <Collapse activeKey = {'panel2'} >
                    <Collapse.Panel title = {'标题 1'} panelKey = {'panel1'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 1 的内容</p>
                    </Collapse.Panel>
                    <Collapse.Panel title = {'标题 2'} panelKey = {'panel2'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 2 的内容</p>
                    </Collapse.Panel>
                    <Collapse.Panel title = {'标题 3'} panelKey = {'panel3'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 3 的内容</p>
                    </Collapse.Panel>
                </Collapse>
                <br/>
                <p className="label">手风琴</p>
                <Collapse accordion = {true} >
                    <Collapse.Panel title = {'标题 1'} panelKey = {'panel1'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 1 的内容</p>
                    </Collapse.Panel>
                    <Collapse.Panel title = {'标题 2'} panelKey = {'panel2'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 2 的内容</p>
                    </Collapse.Panel>
                    <Collapse.Panel title = {'标题 3'} panelKey = {'panel3'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 3 的内容</p>
                    </Collapse.Panel>
                </Collapse>
                <br/>
                <p className="label">回调</p>
                <Collapse accordion = {true} onChange = {(activeKey) => activeKey && this.toast.show({
                    mask: false,
                    content: activeKey
                })} >
                    <Collapse.Panel title = {'标题 1'} panelKey = {'panel1'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 1 的内容</p>
                    </Collapse.Panel>
                    <Collapse.Panel title = {'标题 2'} panelKey = {'panel2'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 2 的内容</p>
                    </Collapse.Panel>
                    <Collapse.Panel title = {'标题 3'} panelKey = {'panel3'} >
                        <p style={{fontSize: '0.35rem', margin: '0.5rem 0'}} >这是标题 3 的内容</p>
                    </Collapse.Panel>
                </Collapse>
                <Toast ref = {toast => this.toast = toast} />
            </div>
        )
    }
}


