import React from 'react'
import Header from '../../h5/component/header'
import Icon from '../../h5/component/icon'

export default class HeaderExample extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div className="container">
                <p className={'label'}>基本</p>
                <Header title = {'导航栏'}/>
                <br/>
                <p className={'label'}>左侧按钮</p>
                <Header
                    title = {'导航栏'}
                    leftBtn = {<Icon name={'ios-arrow-back'} color={'white'}/>}
                />
                <br/>
                <p className={'label'}>右侧按钮</p>
                <Header
                    title = {'导航栏'}
                    rightBtn = {<span style = {{fontSize: '0.35rem', color: 'white'}} >更多</span>}
                />
                <br/>
                <p className={'label'}>自定义样式</p>
                <Header
                    title = {'导航栏'}
                    containerStyle = {{backgroundColor: '#01DD9B'}}
                    leftBtn = {<Icon name={'ios-arrow-back'} color={'white'}/>}
                    rightBtn = {<span style = {{fontSize: '0.35rem', color: 'white'}} >更多</span>}
                />

            </div>
        )
    }
}


